.nav {
  color: $white-dark;
  ul {
    @include flex;
    @include md {
      display: block; } }
  li {
    @include xss {
      @include notlast {
        margin-bottom: 10px; } } }
  a {
    display: block;
    text-transform: uppercase;
    @include tr(color .3s, background-color .3s);
    padding: 25px;
    // padding-right: 40px
    @include xl {
      padding-left: 10px;
      padding-right: 10px; }
    @include lg {
      padding-left: 5px;
      padding-right: 5px; }
    @include xss {
      display: inline;
      padding: 0; }
    &.is-active {
      color: $white;
      background-color: #1f1f1f; }
    @include hover {
      color: $white;
      background-color: #1f1f1f; } }

  &--footer {
    color: $gray-light;
    ul {
      display: block; }
    li {
      display: block;
      @include notlast {
        margin-bottom: 15px;
        @include md {
          margin-bottom: 10px; }
        @include sm {
          margin-bottom: 5px; }
        @include xss {
          margin-bottom: 5px; } } }
    a {
      padding: 0 !important;
      display: inline;
      text-transform: none;
      &.is-active {
        color: $white;
        background-color: transparent; }
      @include hover {
        color: $white;
        background-color: transparent; } } } }
