.cart-item {
  @include flex(flex-start, center);
  position: relative;
  border-bottom: 1px solid  $gray;
  padding-top: 20px;
  padding-bottom: 20px;
  @include xs {
    flex-wrap: wrap; }
  &__img {
    max-width: 20%; }
  &__title {
    flex-grow: 1;
    text-transform: uppercase;
    color: $white-dark;
    @include semi;
    padding-left: 30px;
    padding-right: 10px;
    @include lg {
      padding-left: 15px; }
    small {
      display: block;
      @include light;
      font-size: 13px;
      color: $c-text; } }
  &__price {
    padding-left: 70px;
    @include xl {
      padding-left: 50px; }
    @include lg {
      padding-left: 40px; }
    @include xl {
      width: 50%;
      margin-top: 15px;
      padding-left: 0;
      text-align: right; } }
  &__counter {
    @include xs {
      width: 50%;
      margin-top: 15px;
      @include flex; } }

  &__close {
    @include icon-close(20, 1, currentColor);
    position: absolute;
    top: 15px;
    right: 0;
    z-index: 1; } }
